.logIn-container > div {
    width: 20rem;
    height: 95%;
    margin-right: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--background-color-opacity-9);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    border: 1px solid var(--color-secondary);
    border-radius: 10px;
    transition: color 0.15s ease, border-color 0.15s ease;
}

.logIn-container > div > h2 {
    color: var(--text-white);
}

.logIn-container > div:hover {
  border-color: var(--color-primary);
}

.logIn-inputs-container {
    width: max-content;
    height: 12rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.logIn-inputs-container > label {
    justify-self: flex-start;
    align-self: flex-start;
    color: var(--text-white);
}

.logIn-inputs-container > input {
    background-color: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid var(--text-white);
    color: var(--text-primary);
}

.logIn-inputs-container > span {
    border: 2px solid transparent;
    cursor: pointer;
    font-size: .8rem;
    color: var(--text-white);
    transition: color 0.15s ease, border-color 0.15s ease;
}

.logIn-inputs-container > span:hover {
    color: var(--text-primary);
}

.logIn-inputs-container > button {
    background-color: transparent;
    padding: 0.4rem;
    border: 1px solid var(--color-secondary);
    border-radius: 3px;
    color: var(--text-white);
    cursor: pointer;
    transition: color 0.15s ease, border-color 0.15s ease;
}

.logIn-inputs-container > button:hover {
    background-color: var(--detail-color);
    padding: 0.4rem;
    border: 1px solid var(--color-primary);
    border-radius: 3px;
    color: var(--color-primary);
    cursor: pointer;
}

